.header {
	grid-area: h;
	background-color:#5cc6fe;
	max-width: 1000px;
	border-bottom-left-radius: 11px;
	border-bottom-right-radius: 11px;
	box-shadow: rgba(0,0,0,0.2) 1px 1px 4px;
}

.header > img {
	height: 46px;
	border-radius: 10px;
	overflow: hidden;
}

.loginBlock {
	float: right;
	font-size: 1.2rem;
	line-height: 40px;
	margin-right: 15px;
}

.loginBlock button {
	color: white;
	text-shadow: black 1px 1px 2px;
	text-decoration: none;
	background-color: rgb(198, 255, 144);
	font-size: 1.1rem;
}

.loginBlock span {
	margin-right: 5px;
}